require('./index.css');
/**
 * @example:
 * new Base({
 *  cls:'',//全局modal样式
 *  title : '标题',
 *  msg   : '消息',
 *  inputType : 'text',
 *  btns : [{
 *    text : '取消' ,
 *    cls:''//btn样式
 *  },{
 *    text : '确定',
 *    cls:'',//btn样式
 *    onTap : (value) => {} // 返回false时，点击按钮不会隐藏对话框
 *  }]
 * })
 */

export class Modal {
    constructor(o){
        this.options = o;
        let template = this.createTemplate(this.options);
        this.$modal = $(template).appendTo(document.body);
        this.$box   = this.$modal.find('.modal-box');
        if (this.options.inputType) {
            this.$input = this.$modal.find('.modal-input');
        }
        this.$btns  = this.$modal.find('.modal-btn');
        this.bindEvent();
        this.show();
    }
    createTemplate(opts){ // 生成字符串模板
        let headerStr = opts.title ? `<div class="modal-header">${opts.title}</div>` : '';
        let inputStr  = opts.inputType ? `<input type="${opts.inputType}" class="modal-input">` : '';

        let btnStr = '';
        opts.btns.forEach((item) => {
            btnStr += `<span class="modal-btn ${item.cls||''}">${item.text}</span>`;
        });

        return`<div class="modal-bg modal-hide ${opts.cls||''}">
            <div class="modal-box modal-null">
                ${headerStr}
                <div class="modal-middle">
                    <span>${opts.msg}</span>
                    ${inputStr}
                </div>
                <div class="modal-footer">
                    ${btnStr}
                </div>
            </div>
        </div>`;
    }
    getInputValue(){
        if (this.$input) {
            return this.$input.val().trim();
        }
        return '';
    }
    getTapEvent(text){
        let fn = null;
        this.options.btns.forEach((item) => {
            if (item.text == text) {
                fn = item.onTap;
            }
        });
        return fn;
    }
    bindEvent(){ // 绑定按钮事件
        let me = this;
        this.$btns.on('touchend' ,function (e) {
            e.preventDefault();
            let that = $(this);
            let onTap = me.getTapEvent( that.html() );
            let inputValue = me.getInputValue();
            if (onTap && onTap(inputValue) === false) {
                return ;
            }
            if (me.$input) {
                me.$input.blur();
            }
            me.destroy();
        });
        return this;
    }
    show(){
        this.$modal.removeClass('modal-hide');
        setTimeout(() => {
            this.$box.removeClass('modal-null');
        },10);
        return this;
    }
    hide(fn){ // 通过延迟隐藏，处理击穿Bug
        this.$box.addClass('modal-null');
        setTimeout(() => {
            this.$modal.addClass('modal-hide');
            if (fn) { fn() ;}
        }, 300);
        return this;
    }
    destroy(){
        this.hide(() => {
            this.$modal.remove();
        });
        return this;
    }
}
/**
 * 对话框
 * @example
 * modal.alert('msg', function(){  });
 */
export function alert(str , fn){
    let btn = {text:'确定'};
    if (fn) {
        btn.onTap = fn
    }
    return new Modal({
        msg : str,
        btns: [btn]
    });
}
/**
 * 确认框
 * @example
 * modal.confirm({
 *  msg : 'msg',
 *  ok : () => {}
 * })
 */
export function confirm(o){
    return new Modal({
        title: o.title,
        msg :  o.msg,
        btns: [{
            text : '取消'
        },{
            text : '确定',
            onTap : o.ok
        }]
    });
}
/**
 * 输入框
 * @example
 * modal.prompt({
 *  msg : 'msg',
 *  inputType : 'text',
 *  ok : () => {}
 * })
 */
export function prompt(o){
    return new Modal({
        title : o.title,
        msg   : o.msg,
        inputType : o.inputType || 'text',
        btns  : [{
            text : '取消'
        },{
            text : '确定',
            onTap : o.ok
        }]
    });
}
/**
 * 手机号输入验证组件
 * @example
 * modal.promptPhone({
 *  title : 'title',
 *   msg : 'msg',
 *   ok : (value) => { alert(value); },
 *   error : () => { alert('请输入正确的手机号码'); }
 * });
 */
export function promptPhone(o) {
    let modal = new Modal({
        title : o.title,
        msg   : o.msg,
        inputType : 'tel',
        btns  : [{
            text : o.cancalText || '取消'
        },{
            text : o.okText || '确定',
            onTap : (value) => {
                if (!/^1\d{10}$/.test(value)) {
                    modal.$input.addClass('error');
                    if (o.error) {
                        o.error();
                    }
                    return false;
                }
                modal.$input.removeClass('error');
                return o.ok(value);
            }
        }]
    });
}


