/**
 * 字符串是否为空
 * @param  {string}  str 待验证的字符串
 * @return {Boolean}       
 */

function isEmpty(str) {
    if (typeof str != 'string') {
        return false;
    }
    return str.replace(/\s/g, '') === '';
}

module.exports = isEmpty;


