const isEmpty = require('./is-empty');

/**
 * 字符串是否为空
 * @param  {string}  str 待验证的字符串
 * @return {Boolean}       
 */
function isPhone(str) {
    if (!isEmpty(str)) {
        return /^1[34578]{1}[0-9]{9}$/g.test(str) || /^1011[12]{1}\d{6}$/g.test(str);
    }
    return false;
}

module.exports = isPhone;


