/**
 * 防止按钮重复点击, 需要手动重置
 * 
 * @example:
 * const norepeat = require('norepeat-event');
 * let flag = norepeat.mnul('select').on('tap', function(){
 *      // do something
 * });
 * 
 * switch.reset();
 */
class MnulEvent{
    constructor(selector){
        this.$btn = $(selector);
        this.switch = true;
    }
    reset(){
        this.switch = true;
        return this;
    }
    on(type, fn){
        let me = this;
        this.$btn.on(type, function(){
            if (!me.switch) { return false; }
            me.switch = false;
            return fn.call(this);
        });
        return this;
    }
}
export function mnul(selector){
    return new MnulEvent(selector);
}

/**
 * 防止按钮重复点击, 自动重置
 * @example:
 * const norepeat = require('norepeat-event');
 * norepeat.auto('selector').on('tap', function(){
 *      // do something
 * })
 */

class AutoEvent{
    constructor(selector){
        this.$btn = $(selector);
        this.switch = true; // 是否触发事件的开关

        this.timer  = null;
        this.time   = 1000;
    }
    setResetTime(time){
        this.time = time;
        return this;
    }
    reset(){
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
            this.switch = true;
            if (this.resetEvent) {
                this.resetEvent();
            }
        }, this.time );
        return this;
    }
    onReset(callback){
        this.resetEvent = callback;
        return this;
    }
    on(type, fn){
        let me = this;
        this.$btn.on(type, function(){
            if (!me.switch) { return ; }
            me.switch = false;
            me.reset();
            fn.call(this);
        });
        return this;
    }
}

export function auto(selector){
    return new AutoEvent(selector);
}







